.friendMessage {
    hyphens: auto;
}

.friendImageAndText {
    display: flex;
    gap: 20px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
}

.friendText {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 4px;
    padding: 8px 13px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px 10px 10px 0;
    min-width: 150px;
    max-width: 900px;
}

.friendText::after {
    content: '';
    position: absolute;
    right: 100%;
    bottom: 0;
    width: 20px;
    height: 20px;
    background: radial-gradient(
            circle at 0 0,
            rgba(0, 0, 0, 0) 20px,
            #F5F7FB 0);
}

.friendName {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
}

.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    white-space: normal;
    word-wrap: break-word;
}

.friendTime {
    width: 48px;
    margin-top: 5px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
}
