.sendForm {
    display: flex;
    justify-content: space-between;
    gap: 44px;
    margin-top: 56px;
}

.textarea {
    flex-grow: 1;
    padding: 12px 24px;
    resize: none;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border: none;
    outline: none;
    border-radius: 20px;
}

.button {
    align-self: center;
    padding: 10px 39px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    background: #0066CC;
    border: none;
    border-radius: 20px;
    cursor: pointer;
}

